@import './softSkills.scss';
@import './tools.scss';

.hero {
  @include flex(center, unset, column, 20px);
  font-family: $fontGeneral;
  font-size: 0.9rem;
  height: 35vh;
  &::before {
    @include before__background(0, top, opacity 1s ease-in);
  }
  &:hover::before {
    opacity: 1;
  }
  &__title {
    padding-top: 20px;
    z-index: 1;
    font-weight: bold;
    &__link {
      @include flex(unset, center, column, 10px);
      color: gray;
      text-decoration: none;
      transition: color 0.3s ease, text-shadow 0.3s ease;
      & .title {
        font-size: 1.5rem;
      }
      & .subTitle {
        font-size: 1.2rem;
      }
      & .aboutMeBtn {
        display: none;
        border: none;
        background-color: black;
        color: white;
        margin-top: 15px;
        &:hover {
          color: gray;
          background-color: white;
          border: solid 2px white;
          border-radius: 20%;
          padding: 3px 7px;
        }
      }
    }
  }
  &:hover {
    .hero__title__link {
      color: black;
    }
  }
}

.aboutMeText {
  width: 800px;
  text-align: center;
  margin-top: 5px;
}

//hidden classes
.hidden {
  @include hidden__transition();
}

